import React from 'react';
import {Link} from 'react-router-dom';
import imgShoulderPull from '../assets/shoulder-pull.webp';
import imgShoulderPull2x from '../assets/shoulder-pull-2x.webp';
import CircleW from '../assets/circle-white.svg';
import Carousel from '../components/carousel';

const Home = () => {
  return (
    <div className="font-main">
      <div className="border-zinc-400 border-l-2 h-14 absolute left-1/2 -ml-px my-12"></div>
      <section className="container text-ellipsis pt-44 pb-16">
        <div className="text-p-size max-w-[75ch]">
          <div className="md:border-l-2 md:border-zinc-400 px-10 md:px-0 md:pl-20 md:ml-12 mb-24 pb-12">
            <h2 className="text-h1-size font-medium">Cześć, jestem Natalia.</h2>
            <br />
            <p>
              Moją misją jest promowanie aktywności fizycznej oraz budowanie
              świadomości ciała – tego jak z nim pracować, aby mieć więcej
              energii i stać się zdrowszym, sprawniejszym oraz efektywniejszym
              człowiekiem.
            </p>
            <br />
            <img
              src={imgShoulderPull}
              scrset={`${imgShoulderPull} 1x, ${imgShoulderPull2x} 2x,`}
              className="rounded-small-images my-8"
            ></img>
            <p className="pt-8 pb-4">
              Każdego dnia widzę jak ruch zmienia życie moich podopiecznych na
              lepsze. Ogromną satysfakcję daje mi obserwowanie tego, jak efekty
              wspólnej pracy sięgają daleko poza salę treningową czy samą formę
              fizyczną.
            </p>
            <p>
              Dla mnie odkąd pamiętam, ruch był nieodłączną częścią życia –
              pomagał pozbyć się negatywnych emocji, zadbać o ciało i osiągnąć
              lepsze wyniki w różnych dyscyplinach.
            </p>
          </div>
        </div>
      </section>

      <div className="h-96 w-full bg-white">
        <div className="container">
          <Link to="/oferta">
            <button
              type="button"
              className="rounded-button bg-black text-center font-medium text-white text-p-size absolute h-20 w-96 left-1/2 -ml-48 m-16"
            >
              {' '}
              zobacz ofertę {'>>'}
            </button>
          </Link>
          <div className="border-zinc-400 border-l-2 h-36 absolute left-1/2 -ml-px my-auto mt-48"></div>
        </div>
      </div>

      <section className="container text-ellipsis py-16 md:pl-20">
        <details className="text-p-size max-w-prose">
          <summary className="list-none flex flex-col md:flex-row md:items-baseline">
            <h1 className="text-h1-size font-medium text-center md:text-left pb-6 md:pb-0">
              Certyfikaty
            </h1>
            <img src={CircleW} className="inline md:pr-6 flex-grow-0 self-center md:order-first"></img>
          </summary>
          <br />
          <p>
            Doświadczenie trenerskie zdobywałam pracując na siłowniach oraz w
            klubach sportowych, opiekując się osobami trenującymi zawodowo, jak
            i amatorsko oraz ludźmi, którzy po prostu chcieli się poruszać.
            Ukończyłam wiele kursów, szkoleń i konferencji z zakresu treningu
            personalnego, przygotowania motorycznego oraz dietetyki, które dały
            mi solidną podstawę do pracy z różnorodnymi podopiecznymi. Cały czas
            się uczę, dokształcam i poszerzam swoją wiedzę. Kilka wybranych
            certyfikatów znajdziesz poniżej.
          </p>
          <div className="py-12">
            <Carousel />
          </div>
        </details>
      </section>

      <div className="bg-white">
        <section className="container py-16 relative md:pl-20">
          <details className="text-p-size max-w-prose">
            <summary className="list-none flex flex-col md:flex-row md:items-baseline">
              <h1 className="text-h1-size font-medium text-center md:text-left pb-6 md:pb-0">
                Opinie
              </h1>
              <img src={CircleW} className="inline md:pr-6 flex-grow-0 self-center md:order-first"></img>
            </summary>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Karolina</h2>
              <blockquote className="max-w-prose italic">
                - Z każdej kontuzji, zmieni nastawienie do treningu - teraz to
                sama przyjemność a nie ciągłe zmuszanie się. A co najważniejsze
                - potrafi tak zakorzenić sport we krwi, że już nigdy nie ma się
                dość! Polecam z całego serduszka.
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Michał</h2>
              <blockquote className="max-w-prose italic">
                - Polecam Natalię z całego serca. Pełen profesjonalizm i ogromna
                wiedza i doświadczenie w pracy co przekłada się na efekty. Praca
                z Natalia to nie tylko niezły wycisk, ale też miło spędzony
                czas. Jeżeli ktoś szuka trenera to szczerze polecam -
                odpowiednia osoba na odpowiednim miejscu.
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Agnieszka</h2>
              <blockquote className="max-w-prose italic">
                - Profesjonalne treningi z Natalią w kameralnym miejscu to moja
                ulubiona część dnia! Trenuję z Natalią już pewien czas i widać
                efekty! Świetny kontakt, pełen profesjonalizm i duże
                doświadczenie czyni Natalię REWELACYJNĄ trenerką!!! Polecam z
                całego serca, jeżeli chcesz zmienić swoje życie na lepsze!!! :)
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Katarzyna</h2>
              <blockquote className="max-w-prose italic">
                - Natalia to prawdziwa pasjonatka. Jest profesjonalistką w
                każdym calu. Cierpliwa, ale jednocześnie bardzo wymagająca. Nie
                odpuszcza. Trening z Nią to ciężka praca, przynosząca oczekiwane
                rezultaty. Poza tym to bardzo miła, wartościowa osoba a jej
                uśmiech łagodzi największy ból. Polecam.
              </blockquote>
            </section>
            <section className="text-small-size rounded-standard my-14 bg-content-background shadow-xl p-8">
              <h2 className="font-bold mb-4">Anna</h2>
              <blockquote className="max-w-prose italic">
                - Trening z Natalią to profesjonalnie ułożony plan treningowy -
                wiem po co i nad czym należy pracować - czasem jest wycisk, ale
                w takiej atmosferze z przyjemnością człowiek się męczy :)
                Polecam!!!
              </blockquote>
            </section>
          </details>
        </section>
      </div>
    </div>
  );
};

export default Home;

